header {
  color: #F9F9F0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: background-color 0.5s cubic-bezier(0.23, 1, 0.32, 1), color 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}
header .circle-menu {
  transition: background-color 0.5s cubic-bezier(0.23, 1, 0.32, 1);
  border-radius: 100%;
  background-color: #F9F9F0;
  width: 20px;
  height: 20px;
}
@media (min-width: 992px) {
  header .circle-menu {
    width: 25px;
    height: 25px;
  }
}
header .language-link {
  text-decoration: none;
  position: relative;
}
header .language-link::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 1.2px;
  background-color: #F9F9F0;
  top: calc(100% - 4px);
  left: 0;
  transition: background-color 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}
header.headroom--not-top {
  background-color: #F9F9F0;
  color: #242323;
}
header.headroom--not-top .circle-menu {
  background-color: #242323;
}
header.headroom--not-top .language-link {
  color: #242323;
}
header.headroom--not-top .language-link::after {
  background-color: #242323;
}

